@import "./_settings";
@import "./breakpoints";

$half: 50%;

@keyframes animationGlow {
    0% {
        text-shadow: 0px 0px 20px $color_green; }
    50% {
        text-shadow: 0px 0px 3px $color_green; }
    100% {
        text-shadow: 0px 0px 20px $color_green; } }

h1 {
    font-family: $Roboto_B;
    color: $color_green;
    position: absolute;
    left: 50%;
    top: 30px;
    transform: translateX(-50%);
    text-transform: uppercase;
    font-size: 5rem;
    text-shadow: 0px 0px 20px $color_green;
    animation: animationGlow 5s ease infinite;
    pointer-events: none; }
h1::selection {
    background: rgba(0, 0, 0, 0); }
h2 {
    color: $color_green;
    font-family: $Roboto_R;
    font-size: 2rem;
    opacity: 0.5;
    position: absolute;
    left: 50%;
    bottom: 130px;
    transform: translateX(-50%);
    pointer-events: none; }

h2::selection {
    background: rgba(0, 0, 0, 0); }
.CatBox {
    transform-style: preserve-3d;
    width: 20%;
    pointer-events: none;
    .Cat {
        position: absolute;
        width: 100%;
        left: $half;
        top: $half;
        transform: translate(-$half, -$half); }
    .Cat::selection {
        background-color: rgba(0, 0, 0, 0);
        user-select: none; }

    .CatHair {
        top: calc(#{$half} + 60px);
        left: 0;
        transform: translate(-$half, -$half);
        transform: translateZ(20px); }

    .CatEyes {
        width: 70%;
        transform: translateZ(30px);
        left: 15%;
        top: -15px; }

    .Laser {
        position: absolute;
        width: 20px;
        height: 100vh;
        background: $color_purple;
        transform: rotateX(90deg);
        top: -50vh;
        left: 0;
        box-shadow: 0 0 30px $color_purple;
        opacity: 0;
        transition: opacity 0.2s ease; }

    .LaserActivate {
        opacity: 1; }

    .L1 {
        transform: rotateX(90deg) rotateY(0deg) translateY(50vh);
        left: 27%;
        top: -45vh; }

    .L2 {
        transform: rotateX(90deg) rotateY(90deg) translateY(50vh);
        left: 27%;
        top: -45vh; }

    .R1 {
        transform: rotateX(90deg) rotateY(0deg) translateY(50vh);
        left: 70%;
        top: -45vh; }
    .R2 {
        transform: rotateX(90deg) rotateY(90deg) translateY(50vh);
        left: 70%;
        top: -45vh; } }


footer {
    position: fixed;
    bottom: 0rem;
    color: rgba(255, 255, 255, 0.39);
    font-family: Roboto;
    z-index: 5;
    padding: 1rem 0rem;
    width: 100%;
    text-align: center; }



