@import "./breakpoints";

$color_green: rgb(117, 242, 167);
$color_purple: rgb(172, 85, 229);

$color_bg_light: rgb(234, 234, 234);
$color_bg: rgb(23, 2, 37);
$color_bg_red: #480674;
$wh: rgb(255, 255, 255);


//Fonts
@font-face {
   font-family: 'RobotoR';
   src: url('../FONTS/Roboto-Regular.ttf') format("truetype"); }

@font-face {
   font-family: 'RobotoB';
   src: url('../FONTS/Roboto-Bold.ttf') format("truetype"); }

@font-face {
   font-family: 'RobotoM';
   src: url('../FONTS/Roboto-Medium.ttf') format("truetype"); }

@font-face {
   font-family: 'Anton';
   src: url('../FONTS/Anton-Regular.ttf') format("truetype"); }


$Roboto_R: 'RobotoR';
$Roboto_B: 'RobotoB';
$Roboto_M: 'RobotoM';
$Anton: 'Anton';


::-webkit-scrollbar {
    width:  0.7rem;
    height: 0.7rem;
    @include media("<=phone") {
        width: 0.5rem;
        height: 0.5rem; } }


::-webkit-scrollbar-thumb {
    background: $color_purple; }


::-webkit-scrollbar-track {
    background: $color_bg; }



section {
    width: 100%; }

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    @include media("<=phone") {
        flex-wrap: wrap; } }


.flex-h {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap; }

body {
    margin: 0;
    padding: 0;
    background: linear-gradient(45deg, $color_bg 0%, $color_bg_red 100%);
    width: 100%;
    height: 100vh;
    perspective: 700px;
    overflow: hidden; }
